import Swal from 'sweetalert2';

const alertaModificar = (_id) => {
  Swal.fire({
    title: 'Quieres cancelar la edicin?',
    text: "Estas seguro que quieres cancelar la edicin",
    icon: 'question',
    showCancelButton: true,
    confirmButtonColor: '#212F3D',
    cancelButtonColor: '#D46146',
    confirmButtonText: 'Si',
    cancelButtonText: 'No'
  }).then((result) => {
    if (result.isConfirmed) {
      // Aqu puedes agregar el cdigo para realizar la accin de edicin
      window.history.back();
    }
  });
};

export default alertaModificar;
